import axios from 'axios'

// const urlParams = 'XDEBUG_SESSION_START=14162'
const urlParams = ''


//  axios 
const service = axios.create({
    baseURL: process.env.VUE_APP_BASEURL,
    timeout: 5000 // 
})

// 
service.interceptors.request.use(function (config) {
    // 
    if (urlParams) {
        if (config['url'].indexOf('?') > 0) {
            config['url'] += '&' + urlParams
        } else {
            config['url'] += '?' + urlParams
        }
    }
    return config;
}, function (error) {
    // 
    return Promise.reject(error);
});


// 
service.interceptors.response.use(function (response) {
    return response.data;
}, function (error) {
    window.console.error("--------------------------- request error ---------------------------")
    window.console.error(error)
    const status = error.response.status
    switch (status) {
        case 500:
            service.errMsg("")
            break;
        case 400:
            service.errMsg(error.response.data.message)
            break;
        case 401:
            service.err401()
            break
        case 404:
            service.errMsg('')
            break
    }

    return Promise.reject(error);
});


service.uploads = (form) => {
    return service.post('/api/files/upload', form)
}


service.err401 = () => {
    window.console.warn('---------- axios 401 --------')
}

service.errMsg = (msg) => {
    window.console.warn('---------- axios  --------')
    window.console.error(msg)
}

export default service
